import { Button, TextField, Typography } from "@mui/material";
import { Box } from "@mui/system";
import React, { useContext } from "react";
import { appContext } from "../../context";
import AlertPopup from "../UI/Popup/AlertPopup";

const AddPage = () => {

    const {userIsLogin} = useContext(appContext);

    return (
        <Box component="main">
            <Box sx={{ mb: 3 }} className="title-page">
                <Typography variant="h4" className="note">
                     
                </Typography>
            </Box>
            <AlertPopup props={"mb-2"} />
            <Box className="flex-column gap-1 paper wfull addForm" sx={{ px: 4, py: 2 }}>
                {userIsLogin
                    ?   <>
                            <Box className="flex-column gap-0">
                                <Typography variant="h3">
                                    
                                </Typography>
                                <TextField label=" 32 " variant="outlined" />
                            </Box>
                            <Box className="flex-column gap-0">
                                <Typography variant="h3">
                                    
                                </Typography>
                                <TextField label=" 255 " variant="outlined" multiline />
                            </Box>
                            <Box className="flex-column gap-0">
                                <Box className="flex-row wrap">
                                    <Typography variant="h3">
                                        &nbsp;
                                    </Typography>
                                    <Typography variant="h3" className="note">
                                          
                                    </Typography>
                                </Box>
                                <TextField label=" 128 " variant="outlined" multiline />
                            </Box>
                            <Box className="flex-column gap-0">
                                <Box className="flex-row wrap">
                                    <Typography variant="h3">
                                        &nbsp;
                                    </Typography>
                                    <Typography variant="h3" className="note">
                                         
                                    </Typography>
                                </Box>
                                <TextField label=" 16 " variant="outlined" multiline />
                            </Box>
                        </>
                    :   <Typography variant="h4">
                                   !
                        </Typography>
                }
            </Box>
            {userIsLogin
                ?   <Box className="error-page">
                        <Button 
                            variant="contained" 
                            disableElevation
                            sx={{ mt: 2 }}
                        >
                            
                        </Button>
                    </Box>
                :   <></>
            }
        </Box>
    )
}

export default AddPage;