import React from 'react';
import { ABOUT_ROUTE, ADD_ROUTE, ERROR_ROUTE, ITEM_ROUTE, LOGIN_ROUTE, REG_ROUTE, REMEMBER_ROUTE, STARTPAGE_ROUTE} from "./consts";
import ErrorPage from "../components/pages/ErrorPage";
import Start from "../components/pages/Start";
import ItemPage from '../components/pages/ItemPage';
import AboutPage from '../components/pages/AboutPage';
import AddPage from '../components/pages/AddPage';
import LoginPage from '../components/pages/AuthPages/LoginPage';
import RegPage from '../components/pages/AuthPages/RegPage';
import RememberPass from '../components/pages/AuthPages/RememberPass';

export const publicRoutes = [
    {
        path: STARTPAGE_ROUTE,
        Component: <Start />
    },
    {
        path: ERROR_ROUTE,
        Component: <ErrorPage />
    },
    {
        path: ITEM_ROUTE,
        Component: <ItemPage />
    },
    {
        path: ABOUT_ROUTE,
        Component: <AboutPage />
    },
    {
        path: ADD_ROUTE,
        Component: <AddPage />
    },
    {
        path: LOGIN_ROUTE,
        Component: <LoginPage />
    },
    {
        path: REG_ROUTE,
        Component: <RegPage />
    },
    {
        path: REMEMBER_ROUTE,
        Component: <RememberPass />
    },
]