import { Chip } from "@mui/material";
import React from "react";
// import { top } from "../../../static/top";

const ChipRating = ({rating}) => {

    

    return (
        <>
            {rating > 8
                ?   <Chip
                        className="chip-rate"
                        variant="filled"
                        color="success"
                        label={` ${rating || NaN}`} // (${rating})
                        sx={{ fontWeight: '300', fontSize: 18 }}
                    />
                :   <>
                        {rating < 3
                            ?   <Chip
                                    className="chip-rate"
                                    variant="filled"
                                    color="error"
                                    label={` ${rating || NaN}`}
                                    sx={{ fontWeight: '300', fontSize: 18 }}
                                />
                            :   <Chip
                                    className="chip-rate"
                                    variant="filled"
                                    color="warning"
                                    label={` ${rating || NaN}`}
                                    sx={{ fontWeight: '300', fontSize: 18 }}
                                />
                            }
                    </>
            }
        </>
    )
}

export default ChipRating;