import { Box, Chip, Typography } from "@mui/material";
import React from "react";
import { useNavigate } from "react-router-dom";
import { ABOUT_ROUTE } from "../../routes/consts";

const FooterOnPage = () => {
    //eslint-disable-next-line
    const history = useNavigate();

    return (
        <Box component="footer" sx={{ mb: 2 }}>
            <Box className="flex-column gap-0 note mt-1 aic_f">
                <Typography variant="caption" className="flex-row wfull note">
                        
                </Typography>
                <Box className="flex-row gap-1 wrap">
                    <Box className="flex-row gap-0">
                        <Chip 
                            label=" "
                            variant="outlined"
                            onClick={() => history(ABOUT_ROUTE)}
                        />
                        {/* <Chip 
                            label=""
                            variant="outlined"
                            onClick={() => history(ABOUT_ROUTE)}
                        /> */}
                        <Chip 
                            label="v1.2beta3"
                            variant="outlined"
                            onClick={() => window.open("https://github.com/roso1nik/shaverma-tour/releases", "_blank")}
                        />
                    </Box>
                    <Typography variant="subtitle1">
                        created by&nbsp;
                        <a href="https://github.com/roso1nik" target="_blank" rel="noreferrer noopener">
                            roso1nik
                        </a>
                        {/* &nbsp;// v 1.2 */}
                        {/* &nbsp;w/ love to my gf */}
                    </Typography>
                </Box>
            </Box>
        </Box>
    )
}

export default FooterOnPage;