import { Tooltip, Typography } from "@mui/material";
import { Box } from "@mui/system";
import React, { useContext } from "react";
import { useNavigate } from "react-router-dom";
import { STARTPAGE_ROUTE } from "../../routes/consts";
import SwitchLanguagePrefer from "../UI/Switchers/SwitchLanguagePrefer";
import AddCard from "./Card/AddCard";
import SwitchThemeColor from "./Switchers/SwitchThemeColor";
import { prefers_varsTITLE_LOCAL } from "../../static/prefers_vars";
import { appContext } from "../../context";

const HeaderOnPage = () => {

    const history = useNavigate();

    const {preferCity} = useContext(appContext);

    return (
        <Box
            component="header"
            className="flex-column"
            sx={{ mt: 3 }}
        >
            <Box className="flex-row jcsb wfull">
                <Box className="flex-row gap-1">
                    <SwitchLanguagePrefer />
                    <SwitchThemeColor /> 
                </Box>
                <AddCard />
            </Box>
            <Tooltip title="  " arrow>
                <Typography 
                    variant="h2" 
                    sx={{ fontWeight: '800', width: 'max-content' }}
                    onClick={() => history(STARTPAGE_ROUTE)}
                    className="title"
                >
                    Tour De {prefers_varsTITLE_LOCAL[preferCity]}
                </Typography>
            </Tooltip>
        </Box>
    )
}

export default HeaderOnPage;