import { Chip, Typography } from "@mui/material";
import { Box } from "@mui/system";
import React, { useContext } from "react";
import { appContext } from "../../../context";
import { prefers_vars } from "../../../static/prefers_vars";

const SwitchLanguagePrefer = () => {

    const {preferCity, setPreferCity} = useContext(appContext);

    const SwitchPrefer = () => {
        let i;
        if (preferCity === 0) {
            i = 1;
            setPreferCity(i);
        } else {
            i = 0;
            setPreferCity(i);
        }
    }

    return (
        <Box className="flex-row gap-1 switcher-prefer">
            <Typography
                variant="subtitle" 
                className="note"
                fontSize="small"
            >
                 :
            </Typography>
            <Chip
                label={prefers_vars[preferCity]}
                variant="outlined"
                onClick={SwitchPrefer}
            />
        </Box>
    )
}

export default SwitchLanguagePrefer;