import { IconButton, Tooltip } from "@mui/material";
import { Box } from "@mui/system";
import React, { useContext } from "react";
import { useNavigate } from "react-router-dom";
import AddIcon from '@mui/icons-material/Add';
import LoginIcon from '@mui/icons-material/Login';
import { appContext } from "../../../context";
import LogoutIcon from '@mui/icons-material/Logout';
import { ADD_ROUTE, LOGIN_ROUTE, STARTPAGE_ROUTE } from "../../../routes/consts";

const AddCard = () => {

    const history = useNavigate()

    const {userIsLogin, setUserIsLogin} = useContext(appContext); //, setUserIsLogin

    console.log(userIsLogin)

    const logout = () => {
        setUserIsLogin(false)
    }

    return (
        <Box
            className="flex-row gap-1 note"
        >
            {userIsLogin
                ?   <Tooltip title=" " arrow>
                        <IconButton
                            variant="contained" 
                            sx={{ fontSize: '16px' }}
                            onClick={() => history(ADD_ROUTE)}
                        >
                            <AddIcon />
                        </IconButton>
                    </Tooltip>
                :   <></>
            }
            <Tooltip title={userIsLogin ? '' : ''} arrow>
                <IconButton
                    variant="contained" 
                    sx={{ fontSize: '16px' }}
                    onClick={() => {
                        if (userIsLogin) {
                            logout()
                            history(STARTPAGE_ROUTE)
                        } else {
                            history(LOGIN_ROUTE)
                        }
                    }}
                >
                    {userIsLogin
                        ?   <LogoutIcon />
                        :   <LoginIcon />
                    }
                </IconButton>
            </Tooltip>
        </Box>
    )
}

export default AddCard;