import { Button, TextField, Typography } from "@mui/material";
import { Box } from "@mui/system";
import React, { useContext } from "react";
import { useNavigate } from "react-router-dom";
import { appContext } from "../../../context";
import { REG_ROUTE, REMEMBER_ROUTE, STARTPAGE_ROUTE } from "../../../routes/consts";

const LoginPage = () => {

    const history = useNavigate()

    const {setUserIsLogin} = useContext(appContext);

    const setUserLogin = () => {
        setUserIsLogin(true)
    }

    return (
        <Box component="main">
            <Box sx={{ mb: 3 }} className="title-page">
                <Typography variant="h4" className="note">
                    C 
                </Typography>
            </Box>
            <Box className="flex-column gap-1 paper wfull" sx={{ px: 4, py: 2 }}>
                <Box className="flex-column gap-1 paper wfull addForm" sx={{ px: 4, py: 2 }}>
                    <Box className="flex-column gap-0">
                        <Typography variant="h4">
                            
                        </Typography>
                        <TextField label="  " variant="outlined" />
                    </Box>
                    <Box className="flex-column gap-0">
                        <Typography variant="h4">
                            
                        </Typography>
                        <TextField 
                            label=" " 
                            variant="outlined" 
                            type="password"
                        />
                    </Box>
                    <Button 
                        variant="contained"
                        disableElevation
                        onClick={() => {
                            setUserLogin()
                            history(STARTPAGE_ROUTE)
                        }}
                    >
                        
                    </Button>
                    <Box className="flex-column">
                        <Button 
                            variant="text"
                            disableElevation
                            sx={{ fontSize: '12px' }}
                            onClick={() => history(REG_ROUTE)}
                        >
                               
                        </Button>
                        <Button 
                            variant="text"
                            disableElevation
                            sx={{ fontSize: '12px' }}
                            onClick={() => history(REMEMBER_ROUTE)}
                        >
                              
                        </Button>
                    </Box>
                </Box>
            </Box>
        </Box>
    )
}

export default LoginPage;