import { Button, Chip, Rating, TextField, Typography } from "@mui/material";
import { Box } from "@mui/system";
import React, { useContext, useState } from "react";
import { top } from "../../static/top";
import { appContext } from "../../context";
import ChipRating from "../UI/Chip/ChipRating";
import AlertPopup from "../UI/Popup/AlertPopup";
// import ReturnBackButton from "../UI/Button/ReturnBackButton";
import CommentCard from "../UI/Card/CommentCard";

const ItemPage = () => {

    const {clickNowParse} = useContext(appContext);

    const defValRate = Number(top[clickNowParse][1]);
    const [ratingValue, setRatingValue] = useState(defValRate);

    const [valueComment, setValueComment] = React.useState('');
    const handleChange = (event) => {
        setValueComment(event.target.value);
        console.log(valueComment)
    };


    return (
        <Box 
            component="main" 
            sx={{ mt: 3 }}
            className="flex-column gap-1 itemPage"
        >
            <AlertPopup />
            <Box className="flex-column gap-1 paper wfull" sx={{ px: 4, py: 2 }}>
                {/* <ReturnBackButton /> */}
                <Box className="flex-column gap-1">
                    <Box className="flex-column gap-2 top-block-ITEMPAGE">
                        <Box className="flex-row gap-0 wrap">
                            <ChipRating rating={top[clickNowParse][1]}/>
                            <Chip
                                className="chip-rate"
                                variant="outlined"
                                label={` ${top[clickNowParse][2] || NaN} `}
                                sx={{ fontWeight: '300', fontSize: 18 }}
                            />
                            <Chip
                                className="chip-rate"
                                variant="filled"
                                color="info"
                                label={` ${top[clickNowParse][3] || undefined}`}
                                sx={{ fontWeight: '300', fontSize: 18 }}
                            />
                        </Box>
                        <Box className="flex-column">
                            <Typography variant="h3">
                                {top[clickNowParse][0] || undefined}
                            </Typography>
                            <Typography variant="h5" className="note" sx={{ fontWeight: 400 }}>
                                {top[clickNowParse][4] || '  '}
                            </Typography>  
                        </Box>
                    </Box>
                    <Box className="flex-column gap-1 container ratingForm" sx={{ px: 4, py: 2 }}>
                        <Typography variant="h4" className="title-comment-ITEMPAGE">
                              :
                        </Typography>
                        <Box className="flex-row gap-1 wfull wrap">
                            <Rating 
                                max={10} 
                                defaultValue={ratingValue}
                                onChange={(event, newValue) => {
                                    setRatingValue(newValue);
                                }}
                                size="large"
                                precision={0.5}
                                className="wfull"
                            /> 
                            <Box className="flex-row">
                                <Typography variant="h5" className="note">
                                     :&nbsp;
                                </Typography>
                                <Typography variant="h5">
                                    {ratingValue}
                                </Typography>
                            </Box>
                        </Box>
                        <TextField 
                            label=" ()" 
                            multiline
                            variant="outlined" 
                            value={valueComment}
                            onChange={handleChange}
                        />
                        <Button variant="contained" disableElevation>
                            
                        </Button>
                    </Box>
                    <Box className="flex-column gap-1 bottom-block-ITEMPAGE" sx={{ mt: 1 }}>
                        <Typography variant="h5">
                            :
                        </Typography>
                        <Box className="flex-column gap-0">
                            {top[clickNowParse][5].length !== 0
                                ?   <>
                                        {Object.keys(top[clickNowParse][5]).map(el => (
                                            <CommentCard 
                                                el={el} 
                                                key={Math.random()}
                                            />
                                        ))}
                                    </>
                                :   <Typography variant="body1" className="note">  </Typography>
                            }
                        </Box>
                    </Box>
                </Box>
            </Box>
        </Box>
    )
}

export default ItemPage;