import { Chip, Divider, Typography } from "@mui/material";
import { Box } from "@mui/system";
import React, { useContext } from "react";
import { useNavigate } from "react-router-dom";
import { appContext } from "../../../context";
import { ITEM_ROUTE } from "../../../routes/consts";
import ChipRating from "../Chip/ChipRating";

const TopCard = ({title, rating, price, subway, id, info}) => {

    const history = useNavigate()

    const {setClickNowParse} = useContext(appContext);

    const ClickParse = (el) => {
        setClickNowParse(el)
    }

    return (
        <Box 
            className="flex-row gap-3 wrap eat-card"
            onClick={() => {history(ITEM_ROUTE); ClickParse(id)}}
        >
            <Typography 
                variant="h4" 
                sx={{ fontWeight: '700', width: '15%' }}
            >
                {Number(id) === 0
                    ?   <> {Number(id) + 1}&nbsp;</>
                    :   <> {Number(id) + 1}</>
                }
            </Typography>
            <Divider
                className="divider"
                orientation="vertical" 
                flexItem 
            />
            <Box 
                className="flex-row wrap eat-info gap-0"
                sx={{ width: '70%' }}
            >
                <Box className="flex-row wfull wrap">
                    <Typography 
                        variant="h4" 
                        sx={{ fontWeight: '300', mr: 3 }}
                    >
                        {title || undefined} {/*  */}
                    </Typography>
                    <Box className="flex-row gap-0 wrap">
                        <ChipRating rating={rating} />
                        <Chip
                            className="chip-rate"
                            variant="outlined"
                            label={` ${price || NaN} `}
                            sx={{ fontWeight: '300', fontSize: 18 }}
                        />
                        <Chip
                            className="chip-rate"
                            variant="filled"
                            color="info"
                            label={` ${subway || undefined}`}
                            sx={{ fontWeight: '300', fontSize: 18 }}
                        />
                    </Box>
                </Box>
                <Box className="flex-row gap-1 wrap">
                    <Typography 
                        variant="subtitle" 
                        className="note"
                        sx={{ fontWeight: '300' }}
                    >
                        {info || '  '}
                    </Typography>
                </Box>
            </Box>
        </Box>
    )
}

export default TopCard;