import { Box, Typography } from "@mui/material";
import React, { useContext } from "react";
import { useNavigate } from "react-router-dom";
import { prefers_vars_LOCAL } from "../../static/prefers_vars";
import { appContext } from "../../context";
// import HeaderOnPage from "../UI/HeaderOnPage";
import {top} from "../../static/top"
import TopCard from "../UI/Card/TopCard";


const Start = () => {
    //eslint-disable-next-line
    const history = useNavigate();

    const {preferCity} = useContext(appContext);

    

    return (
        <Box component="main">
            <Box className="title-page">
                <Typography variant="h4" className="note">
                     {prefers_vars_LOCAL[preferCity]}
                </Typography>
            </Box>
            {/* <Box className="flex-row">
                <Typography variant="body1" sx={{ textDecoration: 'underline dashed' }}>
                     
                </Typography>
            </Box> */}
            <Box className="flex-column gap-0" sx={{ mt: 3 }}>
                <Box className="flex-column paper wfull" sx={{ px: 4, pt: 1.5, pb: 2 }}>
                    {Object.keys(top).map(el => (
                        <TopCard 
                            title={top[el][0]}
                            rating={top[el][1]}
                            price={top[el][2]}
                            subway={top[el][3]}
                            id={el}
                            info={top[el][4]}
                            key={Math.random()}
                        />
                    ))}
                </Box>
                {/* <Box className="flex-column">
                    <Box className="flex-row">
                        top 4
                    </Box>
                </Box> */}
            </Box>
        </Box>
    )
}

export default Start;