import { Box, Typography } from "@mui/material";
import React, { useContext } from "react";
import { appContext } from "../../../context";
import { top } from "../../../static/top";

const CommentCard = ({el}) => {

    const {clickNowParse} = useContext(appContext);

    return (
        <>
            {top[clickNowParse][5][el][2] > 8
                ?   <Box 
                        className="flex-column container wfull good-comment" 
                        sx={{ px: 2, py: 1 }}
                    >
                        <Box className="flex-row gap-0">
                            <Typography variant="body1" sx={{ fontWeight: '600' }}>
                                {top[clickNowParse][5][el][0]}
                            </Typography> 
                            <Typography variant="body1" sx={{ fontWeight: '600' }}>
                                &nbsp;{top[clickNowParse][5][el][2] || NaN}
                            </Typography> 
                        </Box>
                        <Typography variant="body2">
                            {top[clickNowParse][5][el][1]}
                        </Typography>
                    </Box>
                :   <>
                        {top[clickNowParse][5][el][2] < 3
                            ?   <Box 
                                    className="flex-column container wfull bad-comment" 
                                    sx={{ px: 2, py: 1 }}
                                >
                                    <Box className="flex-row gap-0">
                                        <Typography variant="body1" sx={{ fontWeight: '600' }}>
                                            {top[clickNowParse][5][el][0]}
                                        </Typography> 
                                        <Typography variant="body1" sx={{ fontWeight: '600' }}>
                                            &nbsp;{top[clickNowParse][5][el][2] || NaN}
                                        </Typography> 
                                    </Box>
                                    <Typography variant="body2">
                                        {top[clickNowParse][5][el][1]}
                                    </Typography>
                                </Box>
                            :   <Box 
                                    className="flex-column container wfull med-comment" 
                                    sx={{ px: 2, py: 1 }}
                                >
                                    <Box className="flex-row gap-0">
                                        <Typography variant="body1" sx={{ fontWeight: '600' }}>
                                            {top[clickNowParse][5][el][0]}
                                        </Typography> 
                                        <Typography variant="body1" sx={{ fontWeight: '600' }}>
                                            &nbsp;{top[clickNowParse][5][el][2] || NaN}
                                        </Typography> 
                                    </Box>
                                    <Typography variant="body2">
                                        {top[clickNowParse][5][el][1]}
                                    </Typography>
                                </Box>
                        }
                    </>
            }
        </>
    )
}

export default CommentCard;